%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}
.chart-block-container {
  canvas {
    height: 370px !important;
    width: 100% !important;
  }
}
.chart-container {
  #linechart {
    .apexcharts-canvas {
      .apexcharts-legend {
        .apexcharts-legend-marker {
          top: 1px !important;
        }
      }
    }
  }
}
.chart-block {
  canvas {
    width: 100% !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.candidcahrt {
  width: 100%;
  height: 470px;
}
.amcharts-export-canvas {
  position: absolute;
  display: none;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $white;
  .canvas-container {
    transition: opacity 0.5s ease-out;
  }
  &.active {
    display: block;
  }
  &.dropbox .canvas-container {
    opacity: 0.5;
  }
}
.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  #toggling-series-flot {
    width: 79% !important;
  }
}
.all-chart {
  .flot-chart-container {
    height: 350px;
  }
}
.pieLabel {
  div {
    color: $white !important;
    input[type="checkbox"] {
      margin: 7px;
    }
  }
}
// knob chart css
.knob-chart {
  min-height: 255px;
  .chart-clock-main {
    position: relative;
    width: 200px;
    margin: 45px auto;
    .clock-large {
      position: absolute;
      left: -48px;
      top: -45px;
    }
    .clock-medium {
      position: absolute;
      left: 2px;
      top: 5px;
    }
    .clock-small {
      position: absolute;
      left: 15px;
      top: 55px;
    }
  }
}
// knob chart
.knob-block {
  position: relative;
  input {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
.knob-input-disable {
  input {
    display: none !important;
  }
}
// apex chart
.apex-chart {
  margin: 0 auto;
}
.apexcharts-toolbar {
  z-index: 1 !important;
}
.apexcharts-menu-item {
  width: 110px;
}
.word-tree {
  height: 400px;
}
// chartist chat
.ct-series-a {
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-default);
  }
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-default);
  }
}
.ct-series-b {
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-secondary);
  }
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-secondary);
  }
}
// chartjs chart css start
#myRadarGraph,
#mypolarareaChart {
  height: 370px !important;
  width: 100% !important;
}
#myDoughnutGraph {
  height: 300px !important;
}
.radar-chartjs {
  margin: 0 auto;
}
/**=====================
     39. Chart CSS End
